import React, { Component } from 'react';
import './item.css'
class Home extends Component {
  constructor(props) {
    super(props)
    this.state = {
      checklike: this.props.checkliked
    }
  }

  onlikechange = async (event) => {

    let promise = new Promise((resolve) => {
      this.setState({
        checklike: !this.state.checklike
      })
      resolve(1);
    })
    promise.then((resolve) => {
      this.props.onlikechange(this.props.item.id, this.state.checklike);

      const requestOptions = {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ id: this.props.item.id, like: this.props.item.like })
      };
      fetch('http://localhost:8000/updatelike', requestOptions)
        .then(response => response.json());
    })

    // Simple POST request with a JSON body using fetch


  }
  onseclectvideo=()=>{
    this.props.onseclectvideo(this.props.item.id);
  }

  render() {
    var { item, index, checkliked } = this.props;

    var checklikerooi = this.state.checklike;
    return (
      <div className="col-lg-3 col-md-3 col-sm-6 col-xs-6 col-xxs-12" key={index} >
        <div className="tm-tours-box-2 exam1" >
          <div onClick={this.onseclectvideo}>
            <img src={'https://img.youtube.com/vi/' + item.idvideo + '/hqdefault.jpg'} alt="image" height='150px' width='250px' />
            <div className="tm-tours-box-2-info">
              <h5 className="margin-bottom-15">{item.videosname.replace(".mp4", "")}</h5>
            </div>
          </div>
          <a className="editzixe" onClick={this.onlikechange}>
            <i className={checklikerooi == true ? "fa fa-thumbs-up bluecolorhover  editlocallike editlocalliked" : "fa fa-thumbs-up bluecolorhover editlocallike"}></i>{item.like}</a>
          <a ><i className="fa fa-eye blue bluecolorhover editlocalview"></i>{item.views}</a>
        </div>

      </div>

    )
  }
}

export default Home;
