import React from 'react';
import styled from 'styled-components';
const Container = styled.div`
height: 100vh;
@media (max-width: 1000px) {
  height: 90vh;
}
  @media (max-width: 769px) {
    height: 80vh;
  }
  @media (max-width: 450px) {
    height: 60vh;
  }
`;
class VideoEmbed extends React.Component {
  state = { isClicked: false };

  handleClick = () => {
    this.setState({ isClicked: !this.state.isClicked });
  };

  render() {

    if (this.props.video) {
      const { video } = this.props;
      const videoId = video.id;
      const url = `http://localhost:8000/video/${videoId}`;
      return (
        <div>
          <Container>
            <div align="center" className="embed-responsive embed-responsive-16by9">
            <iframe width="100%" title="video player" src={'https://www.youtube.com/embed/'+video.idvideo} frameBorder="0" allowFullScreen allowscriptaccess="always" />
          

            </div>
            <h4>{video.videosname}</h4>
          </Container>
        </div>
      );
    }
    return "";
  }
}
export default VideoEmbed;
