import React, { Component } from 'react';
import './item.css'
import Videoitem from './videoitem';
import VideoEmbed from './video'
class Home extends Component {
    constructor(props) {
        super(props)
        this.state = {
            contentvideos: [],
            liked: [],
            videoselect: []
        }
    }
    componentWillMount () {
        if (localStorage && localStorage.getItem('liked')) {
            var liked = JSON.parse(localStorage.getItem('liked'));

            this.setState({
                liked: liked,
            });
        }
 fetch('http://localhost:8000/getallvideoalltoday')
            .then((response) => {
                return response.json();
            })
            .then((myJson) => {

                this.setState({
                    contentvideos: myJson,
                    videoselect: myJson[0]
                })


            });
    }
    onseclectvideo=(id)=> {
      
        var index = this.findIndex(id);
        console.log(index);
        this.setState({
            videoselect:this.state.contentvideos[index]
        })
    }
    onlikechange = (id, checklroi) => {
        var index = this.findIndex(id);
        var liked = this.state.liked;
        // console.log(index);
        if (index != -1) {
            var like = this.state.contentvideos;
            if (checklroi === false) {
                like[index].like = like[index].like - 1;

                liked = liked.filter(item => item !== id);
               
            }
            else {
                like[index].like = like[index].like + 1

                liked.push(id);
            }
            this.setState({
                contentvideos: like,
                liked: liked
            })
            localStorage.setItem('liked', JSON.stringify(liked));
        }

    }
    findIndex = (id) => {
        var items = this.state.contentvideos;
        var re = -1;
        items.forEach((item, index) => {
            if (item.id === id) {
                re = index;
            }
        });
        return re;
    }
    render() {

        var element1 = this.state.contentvideos.map((item, index) => {
            var checkexists = this.state.liked.indexOf(item.id);
            var checkliked = false;
            if (checkexists !== -1) {
                checkliked = true;
            }
            return <Videoitem index={index} item={item} onlikechange={this.onlikechange} checkliked={checkliked} onseclectvideo={this.onseclectvideo}></Videoitem>
        });
      
        var videoselect=this.state.videoselect.length===0? <VideoEmbed video={this.state.contentvideos[0]} />:<VideoEmbed video={this.state.videoselect}/>;
        return (
            <div>
           {videoselect}

                <div className="row">
                    {element1}
                </div>
            </div>
        )
    }
}

export default Home;
